* {
    font-family: "Catamaran", sans-serif;
    font-optical-sizing: auto;
    font-weight: 500;
    font-style: normal;
    box-sizing: border-box;
    margin: 0;
    padding: 0;
}

.header_container {
    display: flex;
    padding: 16px 10px 12px 10px;
    background-color: #d4d4d4;
    justify-content: space-between;
    align-items: center;
    #dropdown-custom-1 {
        background-color: rgb(107, 87, 255);
        color: white;
    } 
    .home_link {
        color: white;
        text-decoration: none;
    }
    h2 {
        line-height: 30px;
    }
}


.upcoming_container {
    padding: 2%;
    background-color: #f3f3f3;
    h2 {
        padding: 10px 0;
    }
    .coming_match {
        padding: 15px;
        border-bottom: 2px solid rgb(78, 54, 255);
        background-color: white;
        margin-bottom: 10px;
        p {
            font-size: 14px;
        }
        .match_day {
            padding-bottom: 8px;
        }
        .text_bold {
            font-weight: bold;
        }
        .img_container{
            padding-top: 12px;
            padding-bottom: 8px;
            display: flex;
            justify-content: center;
            gap: 20px;
            img {
                width: 70px;
                height: auto;
            }
        }
        .team_container {
            padding-bottom: 8px;
            display: flex;
            justify-content: center;
        }
        button {
            padding: 7px 14px;
            border: 1px solid #d4d4d4;
            background-color: white;
            border-radius: 8px;
            cursor: pointer;
            transition: background-color 0.3s ease;
            transition: color 0.3s ease;
            &:hover {
                background-color: rgb(78, 54, 255);
                color: white;
            }
        }
        
        
    }
    #see_more_btn {
        display: block;
        margin: 15px auto;
        background-color: rgb(78, 54, 255);
        color: white;
        padding: 15px 24px;
        border-radius: 12px;
        border: none;
        cursor: pointer;
    }
}

.topscore_container {
    
        padding-top: 20px;
        padding-left: 2%;
        padding-right: 2%;

        .goals_flex {
            display: flex;
            justify-content: space-between;
            h2 {
                padding: 10px 0;
            }
            h3 {
                padding: 10px 0;
                padding-right: 8px;
            }
        }
        
        
        .scorer_table {
            display: flex;
            justify-content: space-between;
            align-items: center;
            border-bottom: 2px solid rgb(78, 54, 255);
            margin-top: 2px;
            padding-bottom: 3px;
            padding-right: 8px;
            &:last-child {
                border-bottom: none;
            }
            
            .left {
                display: flex;
                justify-content: space-around;
                align-items: center;
                gap: 15px;
                img {
                    width: 50px;
                    height: auto;
                    border-radius: 50%;
                }
            }
        }
    #top_see_more_btn {
            padding: 7px 14px;
            border: 1px solid rgb(78, 54, 255);
            background-color: rgb(78, 54, 255);
            color: white;
            border-radius: 8px;
            cursor: pointer;
            display: block;
            margin: 0 auto;
    }
    
}

